          <template v-if="config.mostrarLogo">
            <img :src="logoEbook()" class="footer_logo ml-1" alt="logo" />
          </template>
          
        </v-col>
        <v-col sm="12" md="10" class="pt-0">
          <v-row justify="center">
            <!--INICIO UNIDADES-->
            <v-col v-if="config.mostrarUnidades" sm="2" lg="1" class="pt-0 d-none d-md-block">
              <div class="contBtnUnits text-center">
                <div @click="showUnidades = !showUnidades">
                  <div style="margin-top:0px">{{literal["UNIDADES"]["TITULO"]}}</div>
                  <div class="imgUnits"></div>
                </div>

                <template  v-if="showUnidades">

                  <template v-if="this.ebook [this.$store.state.indexLM].unidades.length < 7">
                    <div id="ContBtnUnits" class="units"
                    :style="'left:'+ -1*(60 + config.tamanioUnidad - 230)+'px;width:'+(config.tamanioUnidad+160)+'px;top: -' + ((this.ebook [this.$store.state.indexLM].unidades.length) * 40 + 40 + 13) + 'px; height: ' + ((this.ebook [this.$store.state.indexLM].unidades.length) * 40 + 40 + 4) + 'px'">
                      <p class="titleunits">{{literal["UNIDADES"]["SUBTITULO"]}}</p>
                      <div class="contHidden" :style="'height: ' + (this.ebook  [this.$store.state.indexLM].unidades.length) * 40 + 'px'">
                        
                        <!-- Se realiza el siguiente for para crear los botonoes de unidades -->
                        <template v-for="(lm, index) in this.ebook  [this.$store.state.indexLM].unidades">
                          <div class="contoptunit" @click="irUnidad({unitIndex:lm.pagInicialIndex, unit: lm.pagInicial})" :key="index">
                            <span class="option">{{ lm.nombre }}</span>
                          </div>
                        </template>
                      </div>
                    </div>
                  </template>

                  <template v-else>
                    <div id="ContBtnUnits" class="units"
                    :style="'width:'+(config.tamanioUnidad+240)+'px;top: -360px; height: 351px; left: '+ -1*(60 + config.tamanioUnidad - 200)+'px'">
                      <p class="titleunits">{{literal["UNIDADES"]["SUBTITULO"]}}</p>
                      <div class="contHidden" :style="'overflow: auto;height: 306px'">
                        
                        <!-- Se realiza el siguiente for para crear los botonoes de unidades -->
                        <template v-for="(lm, index) in this.ebook  [this.$store.state.indexLM].unidades">
                          <div class="contoptunit" @click="irUnidad({unitIndex:lm.pagInicialIndex, unit: lm.pagInicial})" :key="index">
                            <span class="option">{{ lm.nombre }}</span>
                          </div>
                        </template>
                      </div>
                    </div>
                  </template>
                </template>
                
              </div>
            </v-col>
            <!--FINAL UNIDADES-->
            
            <!--Solo aparecera cuando este en modo mobile-->
            <Sidebar></Sidebar>

            <!--INICIO BOTON DOBLE PAGINA-->
            <v-col v-if="$store.state.config.esBinario" sm="2" lg="1" class="pt-0 text-center d-none d-md-block">
              <i id="btnPages" @click="doublePage()" class="icon-double color-11"></i>
            </v-col>
            <!--FINAL BOTON DOBLE PAGINA-->
            <!--INICIO PAGINADO-->
            <v-col sm="3" lg="2" class="pt-1">
              <div class="contBtnPagination">
                <v-img @click="atras()" class="arrowBtn btnPaginationLeft isInactive" 
                      src="@/assets/img/next.svg" height="30" contain></v-img>
                <input class="inputPagination ml-0 mr-0" maxlength="4" type="text" @blur="blur" @focus="focus" @click="focus" :value="mostrarPagina" @keyup.enter="irPagina"/>
                <v-img @click="siguiente()" class="arrowBtn btnPaginationRight" 
                      src="@/assets/img/next.svg" height="30" contain></v-img>
              </div>
            </v-col>
            <!--FINAL PAGINADO-->
            <Zoom class="d-none d-md-block"></Zoom>
            <!--INICIO RECURSOS-->
            <v-col sm="2" lg="1" class="pt-0 text-center">
              <i @click="setModalResource(true);setTypeModalResource('typeResourceView')" :class="(ebook[$store.state.indexLM].desactivarRecurso?'isInactiveCanvas': '') + ' icon-media color-11'"></i>
            </v-col>
            <!--FINAL RECURSOS-->
          </v-row>
        </v-col>

        <!--PDF HELP-->
        <v-col class="d-none d-md-block" sm="1" lg="1">
          <v-img v-if="config.mostrarAyuda" @click="openHelp()" src="@/assets/img/help.png" style="cursor:pointer;" class="float-right" height="35" contain></v-img>
        </v-col>
      </v-row>
    </v-container>
  </footer>
