<script>

import { mapState, mapMutations } from 'vuex'
import $ from "jquery";

import Zoom from "./herramientas/Zoom";
import Sidebar from "./herramientas/Sidebar";

export default {
  name: "Footer",

  /**
   * Aqui se agregar los componentes
   */
  components: {
    Zoom,
    Sidebar
  },
  data: () => ({
    showUnidades: false,
    leftContenedorDoble: 0,
    leftContenedorSingle: 0,
    flag: true
  }),
  methods: {
      logoEbook(){
        try{
            let logo = this.$store.state.ebook[this.$store.state.indexLM].logo;
            let existeImg = require("@/assets/img/" + logo); 
            return existeImg;
          } catch {
            return ''
        }
    },
    /**
     * Abre el pdf HELP
     */
    openHelp(){
      window.open("recursos/DOCUMENTOS/help.pdf", "_blank");
    },
    doublePage() {
      if ($("#viewer").hasClass("double")) {
        $("#viewer").addClass("single");
        $("#viewer").removeClass("double");

        $("#btnPages").addClass("icon-document");
        $("#btnPages").removeClass("icon-double");
        this.setTwoPages(false);
      } else if ($("#viewer").hasClass("single")) {

        $("#viewer").removeClass("single");
        $("#viewer").addClass("double");

        $("#btnPages").addClass("icon-double");
        $("#btnPages").removeClass("icon-document");
        this.setTwoPages(true);

        if(this.$store.state.index != 0 && this.$store.state.index % 2 != 1){
          this.$store.state.index--;
        }

        if(this.$store.state.index == 0 || this.$store.state.index == this.ebook[this.$store.state.indexLM].paginas.length - 1){
          $("#viewer").addClass("single");
        }

        this.$store.commit('limites', this.twoPages);
      }
      this.$store.commit('resetearCanvas');
    },

    /**
     * Va a la pagina que se ingreso en la caja de paginas
     */
    irPagina(e){
      
      let pagina = e.target.value;
      pagina = pagina.trim();
      // if(this.isMobile){
        
      //   this.buscarPaginaMobile(pagina);

      // } else {
        this.buscarPagina(pagina);
      // }
      
      e.target.value = '';
    },

    irUnidad(payload){
      this.showUnidades = false;
      this.buscarLMUnidad(payload);
      // this.buscarPagina(pagina);
    },

    /**
     * Cuando se enfoca en la caja de paginas, este limpia su contenido
     */
    focus(e){
      e.target.value = '';
    },

    /**
     * Cuando no se enfoca en la caja de paginas, este lo rellena en la pagina que se encuentra actualmente
     */
    blur(e){
      e.target.value = this.mostrarPagina;
    },
    ...mapMutations(["setModalResource", "setTwoPages", "buscarPagina", "buscarPaginaMobile", "setTypeModalResource", "buscarLMUnidad", "siguiente", "atras"]) 
  }, 
  created() {
    //Se crea eventos de teclado para la tecla izquierda y derecha
    window.addEventListener('keydown', (e) => {
      if (e.key == 'ArrowRight' && !this.openResource && !$(".inputPagination").prop('disabled')) {
       this.siguiente();
      }
      if (e.key == 'ArrowLeft' && !this.openResource && !$(".inputPagination").prop('disabled')) {
       this.atras();
      }
    });
  },
  mounted: function(){
    if(this.$store.state.index == 0){
      let viewer = document.getElementById('viewer')
      viewer.classList.add('single')
    }
  },
  computed: {

    /**
     * Muestra las paginas en la caja de paginas
     */
    mostrarPagina() {
      let numberPageOne = '';
      let numberPageTwo = '';
      
      this.service.setIndexEbook({"page":this.$store.state.index, "lm":this.$store.state.indexLM})
      if(this.$store.state.index == 0 || this.$store.state.index == this.ebook[this.$store.state.indexLM].paginas.length - 1){ 
        return this.ebook[this.$store.state.indexLM].paginas[this.$store.state.index]
      }
      
      if(this.twoPages)
        return this.ebook[this.$store.state.indexLM].paginas[this.$store.state.index] + ` - ` + this.ebook[this.$store.state.indexLM].paginas[this.$store.state.index + 1]
      else
        
        return this.ebook[this.$store.state.indexLM].paginas[this.$store.state.index]
    },
    ...mapState(['literal', 'ebook','twoPages', 'isMobile', 'service', 'openResource', 'config'])
  }
};
</script>

