  width: 90%;
  position: relative;
  margin: 30px auto 15px;
}
.centradoCrono {
  position: relative;
  width: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: no-wrap;
}
.startButtonCrono, .pauseButtonCrono, .resetButtonCrono {
  padding:4px 6px;
  border-radius: 10px;
  background-color: white;
  color: #fff;
  font-family: 'Open Sans';
  cursor: pointer;
  width: 100%;
  font-size: 90%;
  text-align: center;
  margin: 5px 6px;
  padding: 6px 12px;
  text-align: center;
}

.valuesCrono{
  text-align: center;
  font-size: 90%;
  padding: 5px 0;
  background-color: #adadad;
  border-radius: 10px;
  margin-top: -10px;
  margin-bottom: 10%;
  font-weight: bold;
   color: #3c3c3b
}
.startCrono{
  color: gray
}
.resetCrono{
 color: gray
}

.cropper-container {
  z-index: 1;
}

/* .modalPage_btn-cerrar, */
.cronometroCont-cerrar,
.addLinkBtnCont-cerrar {
  width: 30px;
  height: 30px;
  background-size: 30px 30px;
  background-image: url('../../assets/img/equis.png');
  display: block;
  position: absolute;
  cursor: pointer;
  z-index: 9;
  right: -14px;
  top: -14px;
  transition: all .1s ease-in;
}

/* .modalPage_btn-cerrar:hover, */
